body {
  padding-top: 80px;
  font-feature-settings: "palt";
  text-size-adjust: 100%;
  font-family: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

.header {
  width: 100vw;
  height: 80px;
  position: fixed;
  background-color: #fff;
  top: 0;
}
.header__inner {
  padding-right: 10px;
  padding-left: 15px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__left-box {
  display: flex;
  align-items: center;
}
.header__right-box {
  display: flex;
}
.header__tel {
  display: flex;
}
.header__text-box {
  padding-right: 23px;
}
.header__text01 {
  text-align: right;
  padding-bottom: 10px;
  font-size: 12px;
  line-height: 20px;
}
.header__text02 {
  padding-left: 5px;
  font-weight: bold;
}
.header__btn01 {
  width: 142px;
  height: 50px;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  margin-right: 10px;
  background-color: #f5a623;
}
.header__btn01-link {
  font-weight: bold;
}
.header__btn02 {
  width: 142px;
  height: 50px;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  background-color: #1b224c;
}
.header__btn02-link {
  font-weight: bold;
}

.gnav__list {
  display: flex;
}
.gnav__link {
  padding-left: 28px;
  font-size: 16px;
  line-height: 29px;
  font-weight: bold;
}

.fv {
  background-image: url(../images/top/fv.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 476px;
  position: relative;
  text-align: center;
}
.fv__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fv__title {
  font-size: 48px;
  line-height: 65px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 28px;
}
.fv__text {
  font-size: 18px;
  line-height: 31px;
  color: #fff;
  margin-bottom: 51px;
}
.fv__btn {
  height: 60px;
  width: 260px;
  background-color: #f5a623;
  border-radius: 31px;
  color: #fff;
  margin-bottom: 22px;
}
.fv__link {
  display: block;
  line-height: 29px;
  font-weight: bold;
}
.fv__contact {
  display: inline-block;
  color: #fff;
  border-bottom: 1px solid #fff;
}

.about {
  background-color: #1b224c;
  color: #fff;
  text-align: center;
  position: relative;
}
.about__inner {
  padding: 63px 190px 54px 190px;
}
.about__title {
  font-size: 36px;
  line-height: 50px;
  padding-bottom: 65px;
}
.about__text-box {
  display: flex;
  justify-content: center;
  padding-bottom: 83px;
}
.about__text {
  font-size: 18px;
  font-weight: bold;
  line-height: 39px;
  padding-right: 56px;
}
.about__text:nth-child(2) {
  padding-right: 30px;
}
.about__text:nth-child(3) {
  padding-right: 0;
}
.about__sub-textBox {
  width: 100%;
  background-color: #fff;
  color: #1b224c;
  padding-top: 32px;
  padding-bottom: 36px;
  outline: 5px solid #1b224c;
  outline-offset: -16px;
  box-sizing: border-box;
}
.about__sub-title {
  font-size: 36px;
  line-height: 50px;
  padding-bottom: 25px;
}
.about__sub-text {
  font-size: 18px;
  line-height: 31px;
}
.about__under-line {
  background: linear-gradient(transparent 70%, #f5a623 0%);
}